import React from 'react';
import { NavLink } from 'react-router-dom';
import '../styles/Menu.css';

const Menu = () => {
  return (
    <nav className='navigation'>
      <NavLink className={({ isActive }) => `${isActive ? 'active-link' : ''} linkMenu`} to='/Recruitment'>Encuesta de satisfaccin (Reclutamiento)</NavLink>
      <NavLink className={({ isActive }) => `${isActive ? 'active-link' : ''} linkMenu`} to='/Training'>Encuesta de satisfaccin (Onboarding)</NavLink>
      <NavLink className={({ isActive }) => `${isActive ? 'active-link' : ''} linkMenu`} to='/Environment'>Encuesta de clima laboral</NavLink>
      <NavLink className={({ isActive }) => `${isActive ? 'active-link' : ''} linkMenu`} to='/Quit'>Encuesta de salida</NavLink>
      <NavLink className={({ isActive }) => `${isActive ? 'active-link' : ''} linkMenu`} to='/Reject'>Encuesta de desvinculacin</NavLink>
      <NavLink className={({ isActive }) => `${isActive ? 'active-link' : ''} linkMenu`} to='/Home'>Dashboard</NavLink>
    </nav>
  )
}

export default Menu;